<template>
  <div class="nearby">
      <div class="search">
        <input type="text" class="search_box" placeholder="您的位置">
        <span class="ico iconfont icon-sousuo1"></span>
      </div>
      <div class="position clearfix">
          <span class="fl two_t_m">所有的得乐 Café</span>
      </div>
      <div class="list">
          <ul>
              <li v-for="(item,index) in store_list" :key="index" class="two_t_h area clearfix">
                  <p class="clearfix" @click.stop="showStore(index)">
                    <span class="quyu fl">{{item.area}}</span>
                    <span class="iconfont icon-jiahao fr" :class="{'icon-jianhao':c_index==index&&showStoreList}"></span>
                  </p>
                    <transition enter-active-class="fadeInUp">
                        <ul v-show="c_index==index&&showStoreList">
                            <li v-for="(store,s_inedx) in item.store" :key="s_inedx" @click="$router.push('/home/storedetail')" class="s_name two_t_m">
                                {{store.name}}
                                <span class="dian"></span>
                            </li>
                        </ul>
                    </transition>
              </li>
          </ul>
      </div>
  </div>
</template>

<script>
export default {
  name: "Nearby",
  data() {
    return {
      showStoreList: false,
      store_list: [
        {
          area: "福田区",
          store: [
            {
              name: "中洲店"
            },
            {
              name: "宝安中兴店"
            },
            {
              name: "兴东店"
            }
          ]
        },
        {
          area: "南山区",
          store: [
            {
              name: "中洲店"
            },
            {
              name: "宝安中兴店"
            },
            {
              name: "兴东店"
            }
          ]
        },
        {
          area: "罗湖区",
          store: [
            {
              name: "中洲店"
            },
            {
              name: "宝安中兴店"
            },
            {
              name: "兴东店"
            }
          ]
        },
        {
          area: "宝安区",
          store: [
            {
              name: "中洲店"
            },
            {
              name: "宝安中兴店"
            },
            {
              name: "兴东店"
            }
          ]
        }
      ],
      c_index: 99
    };
  },
  methods: {
    showStore(i) {
      if (this.c_index == i) {
        this.showStoreList = false;
        this.c_index = 99;
      } else {
        this.c_index = i;
        this.showStoreList = true;
      }
    }
  },
  components: {}
};
</script>

<style scoped lang="less">
.search {
  font-size: 0.16rem;
  position: relative;
  .search_box {
    width: 100%;
    height: 0.4rem;
    border: none;
    line-height: 0.41rem;
    background: rgba(0, 0, 0, 0.05);
    border-radius: 0.06rem;
    padding-left: 0.43rem;
  }
  .ico {
    position: absolute;
    top: 0.11rem;
    left: 0.15rem;
    color: rgba(0, 0, 0, 0.5);
    font-size: 0.18rem;
  }
}
.position {
  padding: 0.3rem 0rem 0.05rem 0rem;
}
.list {
  ul {
    .area {
      line-height: 0.52rem;
      border-bottom: 0.01rem solid rgba(0, 0, 0, 0.05);
      &:nth-last-child(1) {
        border: none;
      }
      .quyu {
        width: 3rem;
      }
      .s_name {
        position: relative;
        padding-left: 0.1rem;
        .dian {
          position: absolute;
          width: 0.03rem;
          height: 0.03rem;
          border-radius: 50%;
          left: 0rem;
          top: 0.25rem;
          background-color: rgba(0, 0, 0, 0.5);
        }
      }
    }
  }
}
</style>



