export default {
  name: "Nearby",
  data() {
    return {
      showStoreList: false,
      store_list: [
        {
          area: "福田区",
          store: [
            {
              name: "中洲店"
            },
            {
              name: "宝安中兴店"
            },
            {
              name: "兴东店"
            }
          ]
        },
        {
          area: "南山区",
          store: [
            {
              name: "中洲店"
            },
            {
              name: "宝安中兴店"
            },
            {
              name: "兴东店"
            }
          ]
        },
        {
          area: "罗湖区",
          store: [
            {
              name: "中洲店"
            },
            {
              name: "宝安中兴店"
            },
            {
              name: "兴东店"
            }
          ]
        },
        {
          area: "宝安区",
          store: [
            {
              name: "中洲店"
            },
            {
              name: "宝安中兴店"
            },
            {
              name: "兴东店"
            }
          ]
        }
      ],
      c_index: 99
    };
  },
  methods: {
    showStore(i) {
      if (this.c_index == i) {
        this.showStoreList = false;
        this.c_index = 99;
      } else {
        this.c_index = i;
        this.showStoreList = true;
      }
    }
  },
  components: {}
};



